*{
  padding:0;
  margin:0;
}
body{
  background-color: rgb(185, 185, 185);
}
ul,li{
  list-style: none;
}
.headerwrap header{
  width:100%;
  margin:  0 auto;
  background-color: rgb(58, 58, 58);
  height: 50px;
}
.headerwrap img{
  width:120px;
  margin-left: 100px;
  margin-top: 8px;
  float: left;
}
.headerwrap input{
  width: 15%;
  height: 30px;
  border-radius: 15px;
  border:none;
  filter: brightness(0.5);
  transition: filter 0.2s linear;
  text-align: center;
  margin-left: 30px;
  float: left;
  margin-top: 10px;
}
.headerwrap input:hover{
  filter: brightness(1);
  transition: filter 0.2s linear;
}
.headerwrap input:focus{
  filter: brightness(1);
  outline: none;
}
.headerwrap ul{
  margin-top: 15px;
  margin-right: 100px;
}
.headerwrap li{
  float: left;
}
.headerwrap li span{
  margin-left: 15px;
  color:rgb(168, 168, 168);
  transition: color 0.2s linear;

}
.headerwrap li span:hover{
  color: white;
  transition: color 0.2 linear;
}
.main{
  width: 100%;
  margin-top: 20px;
}
.content{
  width:69%;
  float: left;
  margin-left: 5%;
  border:3px solid rgb(255, 255, 255);
  background-color: white;
}
.nav li{
  float: left;
  width:50px;
  height: 20px;
  margin-left: 20px;
  margin-top: 10px;
  text-align: center;
}
.nav li:visited{
  background-color: lightsalmon;
}
.nav{
  width:100%;
  background-color: rgb(100, 100, 100);
  height: 40px;
}
.sider{
  width:20%;
  float: right;
  margin-right:5% ;
}
.content_main li{
  margin-top: 5px;
}
.content_main li:hover{
  background-color: rgb(172, 172, 172);
}
.title:hover{
  cursor: pointer;
}
.tab{
  width:40px;
  height: 20px;
  border-radius: 5px;
  background-color:rgb(148, 148, 148);
  text-align:center;
  line-height: 20px;
  font-size: 12px;
  color: white;
  float: left;
  margin-left: 20px;
  margin-top: 15px;
  overflow: hidden;
}
.title{
  font-size: 16px;
  float: left;
  margin-top: 17px;
  margin-left: 20px;
  font-weight: 1000;
}
.title:hover{
  transition: color 0.1s linear;
  color: rgb(0, 117, 20);
}
.pages{
  height:30px;
}
.pages li{
  float: left;
  width:30px;
  height:30px;
  border:1px solid gray;
  line-height: 30px;
  text-align: center;
}
.sider_top{
  height: 100px;
  background-color: white;
}
.sider_middle{
  height: 200px;
  margin-top: 20px;
  background-color: white;
}
.sider_bottom{
  margin-top: 20px;
  height: 500px;
  background-color: white;
}
.login{
  text-align: center;
  width:350px;
  margin-left: 25%;
  border: rgb(0, 92, 0) 5px solid;
  height: 200px;
  border-radius: 10px;
}
.user{
  width:250px;
  height:35px;
  border-radius: 5px;
  outline: none;
  border: 1px gray solid;
  float: left;
  margin-left: 10px;
  font-size: 18px;
  background-color: rgb(206, 206, 206);
  margin-top: 20px;
}
.pwd{
  width:250px;
  height:35px;
  border-radius: 5px;
  outline: none;
  border: 1px gray solid;
  float: left;
  margin-left: 25px;
  font-size: 30px;
  background-color: rgb(206, 206, 206);
}
.user:hover{
  transition: background-color 0.2s linear;
  background-color: rgb(235, 235, 235);
}
.pwd:hover{
  transition: background-color 0.2s linear;
  background-color: rgb(235, 235, 235);
}
.user:focus{
  background-color: rgb(235, 235, 235);
}
.pwd:focus{
  background-color: rgb(235, 235, 235);
}
.submit{
  border: none;
  outline: none;
  background-color: rgb(82, 100, 255);
  border-radius: 5px;
  width: 250px;
  height: 40px;
  text-align: center;
  line-height: 30px;
  font-size: 15px;
  cursor: pointer;
  margin-top: 15px;
  margin-left: 5%;
}
.submit:hover{
  background-color: rgb(64, 77, 197);
}
.submit:active{
  background-color: rgb(42, 51, 134);
  color:white;
}
.text{
  display: block;
  height: 35px;
  line-height: 35px;
  text-align: center;
  float: left;
}
.login_pwd{
  margin-top: 20px;
  float: left;
}
.text:first-of-type{
  margin-top: 20px;
}